.individual-img {
	width: 60px;
	height: 60px;
	object-fit: cover;
	border-radius: 50%;
}

.business-img {
	width: 54px;
	height: 40px;
	object-fit: cover;
	border-radius: 4px;
}

findqo-button.phone {
	height: 36px;
	width: 100%;
	&.w-343px {
		max-width: 343px !important;
	}
}

.desktop-contacts {
	border: 1px solid var(--neutral-2);
	border-radius: 4px;
}

.mobile-contacts {
	z-index: 2;
}

.report-ad {
	height: 40px;
	margin-top: 16px;
	line-height: 40px;
	text-align: center;
	cursor: pointer;

	.mat-icon {
		display: inline-block !important;
		width: 18px;
		height: 18px;
		vertical-align: middle;
		margin-inline-end: 8px;
	}
}

.agent-img {
	object-fit: cover;
	border-radius: 50%;
}

.dealer-summary {
	background-color: var(--general-1);
	border-radius: 8px;
}

.mat-icon.phone-icon {
	display: inline-block !important;
	width: 18px;
	height: 18px;
	vertical-align: middle;
	margin-inline-end: 8px;
	font-size: 18px;
}

a.view-listing {
	height: 40px;
	line-height: 40px;
}
.display-none {
	display: none !important
	;
}
